html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Exo", sans-serif;
  font-size: 1rem;
  background-color: #EFEFEF;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 0;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

input {
  display: none;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.5rem;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 3.5rem 0 2rem;
  height: 6rem;
  background-color: #EFEFEF;
}
.header a {
  padding: 0.4rem 1.5rem 0.3rem 1.5rem;
  font-size: 1.5rem;
  color: #112B3C;
  border-bottom: 0.1rem solid transparent;
  border-radius: 0.3rem;
  background-color: transparent;
}
.header a:hover {
  border-bottom: 0.1rem solid #F66B0E;
  background-color: #dfe0e0;
}
.header a:active {
  background-color: #FFFFFF;
}
.header .navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header .navigation a {
  margin-right: 2rem;
}
.header .navigation .logo {
  background-color: transparent;
  border-bottom: 0rem;
  margin: 0;
  padding-right: 0;
}
.header .navigation .logo img {
  width: 3rem;
  height: auto;
}
.header .navigation .title {
  padding: 0 2rem 0 1rem;
  margin-right: 3rem;
  font-family: "Satisfy", cursive;
  font-size: 2.5rem;
  color: #F66B0E;
  background-color: transparent;
  border-bottom: 0rem;
}
.header .navigation .title span {
  color: #112B3C;
}
.header .access {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header .access a {
  margin-left: 2rem;
}
.header .access .signup {
  padding: 0.4rem 1.5rem 0.3rem 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: #EFEFEF;
  border-radius: 0.3rem;
  background-color: #F66B0E;
}
.header .access .signup:hover {
  background-color: #d6522a;
}
.header .access .signup:active {
  background-color: #F66B0E;
}

.footer {
  display: block;
}
.footer .contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #112B3C;
  padding: 2rem 15rem 0 15rem;
  height: 15rem;
}
.footer .contact a {
  padding: 0.4rem 1.5rem 0.3rem 1.5rem;
  font-size: 1.5rem;
  color: #112B3C;
  border-bottom: 0.1rem solid transparent;
  border-radius: 0.3rem;
  background-color: #EFEFEF;
}
.footer .contact a:hover {
  border-bottom: 0.1rem solid #F66B0E;
  background-color: #F66B0E;
}
.footer .contact a:active {
  background-color: #FFFFFF;
}
.footer .contact .networks {
  margin-left: 9rem;
}
.footer .contact .networks a {
  background-color: transparent;
  border-bottom: 0;
  padding: 0;
  margin-left: 2rem;
}
.footer .contact .networks .icon {
  font-size: 3rem;
  color: #EFEFEF;
}
.footer .contact .networks .icon:hover {
  color: #F66B0E;
}
.footer .contact .networks .icon:active {
  color: #FFFFFF;
}
.footer .navigation-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #112B3C;
  padding: 0 20rem;
  height: 3rem;
}
.footer .navigation-footer a {
  font-size: 1.3rem;
  padding: 0.1rem 0.5rem;
  margin: 0 15rem;
  color: #EFEFEF;
  border-bottom: 0.1rem solid transparent;
}
.footer .navigation-footer a:hover {
  border-bottom: 0.1rem solid #d6522a;
}
.footer .text-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 2rem;
  text-align: center;
  letter-spacing: 0.3rem;
  background-color: #000000;
}
.footer .text-footer p {
  font-size: 1.2rem;
  color: #EFEFEF;
}